class PagingService {
  sortOptions = [
    {
        value: 'name|asc',
        label: 'Name (Ascending A to Z)'
    },
    {
        value: 'name|desc', 
        label: 'Name (Descending Z to A)'
    }
  ]

  applyFilters = (dataItems, query, filterPropertiesBy) => {
    return dataItems.filter((dataItem) => {
      let matches = true
      
      if(query){
        //const properties = ['name']
        let containsQuery = false

        filterPropertiesBy.forEach((property) => {
          if(dataItem[property].toLowerCase().includes(query.toLowerCase())){
            containsQuery = true
          }
        })

        if(!containsQuery){
          matches = false
        }
      }
      return matches
    })
  }

  applyPagination = (dataItems, page, limit) => {
    const take = page - 1
    return dataItems.slice(take * limit, take * limit + limit)
  }

  descendingComparator = (a, b, orderBy) => {
    if(b[orderBy] < a[orderBy]){
      return -1
    }

    if(b[orderBy] > a[orderBy]){
      return 1
    }

    return 0
  }

  getComparator = (order, orderBy) => {
    return order === 'desc'
      ? (a, b) => this.descendingComparator(a, b, orderBy)
      : (a, b) => -this.descendingComparator(a, b, orderBy)
  }

  applySort = (dataItems, sort) => {
    const [orderBy, order] = sort.split('|')
    const comparator = this.getComparator(order, orderBy)
    const stabilizedThis = dataItems.map((el, index) => [el, index])

    stabilizedThis.sort((a, b) => {
      //eslint-disable-next-line no-shadow
      const order = comparator(a[0], b[0])

      if(order !== 0) return order

      return a[1] - b[1]
    })

    return stabilizedThis.map((el) => el[0])
  }
}

const pagingService = new PagingService()

export default pagingService