import React, { useEffect, useState, useRef, useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { TRANSLATE_NS, PAGING as defaultPaging } from 'src/constants';
//import SplashScreen from 'src/components/SplashScreen';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import equipmentsService from 'src/services/equipmentService';
import { arcAction } from 'src/utils/NotificationUtil';
import Page from 'src/components/Page';
import Header from './Header';
import Results from './Results';
import Paging from './Paging';
import DeleteConfirmationDialog from 'src/components/dialog/DeleteConfirm';

import { Box, Container, makeStyles } from '@material-ui/core';

const sortOptions = [
 {
  value: 'name|asc',
  label: 'Name (Ascending A to Z)'
 },
 {
  value: 'name|desc',
  label: 'Name (Descending Z to A)'
 }
];

const useStyles = makeStyles(theme => ({
 root: {
  backgroundColor: theme.palette.background.default
 },
 content: {
  marginTop: 60
 }
}));

function EquipmentListView() {
 const classes = useStyles();
 const isMounted = useIsMountedRef();
 const { t } = useTranslation([
  TRANSLATE_NS.EQUIPMENTS,
  TRANSLATE_NS.TRANSLATIONS
 ]);
 const [loading, setLoading] = useState(true);
 const [equipments, setEquipments] = useState(null);
 const [query, setQuery] = useState('');
 const [sort, setSort] = useState(sortOptions[0].value);
 const [page, setPage] = useState(defaultPaging.PAGE);
 const [limit, setLimit] = useState(defaultPaging.PAGE_SIZE);
 const [openDeleteConfirm, setOpenDeleteConfirm] = useState(false);
 const currentDataItem = useRef(null);

 const handleQueryChange = value => {
  setQuery(() => value);
  getEquipmentsFromApi(value, sort, page, limit);
 };

 const handlePageChange = newPage => {
  setPage(() => newPage);
  getEquipmentsFromApi(query, sort, page, limit);
 };

 const handleLimitChange = value => {
  setLimit(() => value);
  getEquipmentsFromApi(query, sort, page, value);
 };

 const handleSortChange = value => {
  setSort(() => value);
  getEquipmentsFromApi(query, value, page, limit);
 };
 const handleRemove = dataItem => {
  currentDataItem.current = dataItem;
  setOpenDeleteConfirm(true);
 };

 const getMessage = () => {
  if (currentDataItem.current) return currentDataItem.current?.name;
  else return null;
 };

 const confirmRemove = async () => {
  setOpenDeleteConfirm(false);
  try {
   const result = await equipmentsService.remove(currentDataItem.current);

   console.log(result);

   if (result.resultFlag) {
    arcAction.success('Removed success!');
    getEquipmentsFromApi(query, sort, page, limit);
   }
  } catch (error) {
   arcAction.error(error.errorMessage);
  }
 };

 const getEquipmentsFromApi = useCallback(
  async (query, sort, page, limit) => {
   setLoading(true);
   try {
    const result = await equipmentsService.getList(query, sort, page, limit);
    if (!isMounted.current) return;

    if (result.resultFlag) {
     setEquipments(() => ({ ...result.value }));
    } else {
     //setError(result.errors);
     arcAction.error(result.errors);
    }
   } catch (error) {
    if (isMounted.current)
     //setError(error.message);
     arcAction.error(error.errorMessage);
   } finally {
    if (isMounted.current) setLoading(false);
   }
  },
  [isMounted]
 );

 useEffect(() => {
  getEquipmentsFromApi(
   '',
   'name|asc',
   defaultPaging.PAGE,
   defaultPaging.PAGE_SIZE
  );
 }, [getEquipmentsFromApi]);

 //  if (!equipments) {
 //   return <SplashScreen />;
 //  }

 console.log('-----------render-----------', equipments);

 return (
  <Page className={classes.root} title={t('equipments:register medical tools')}>
   <Header onSearch={handleQueryChange} />
   {equipments && (
    <Container maxWidth="lg" className={classes.content}>
     <Box>
      <Results
       loading={loading}
       equipments={equipments.data}
       itemCounts={limit}
       onSortChange={handleSortChange}
       onRemove={handleRemove}
       limit={limit}
      />
      <Paging
       mt={1}
       page={page}
       limit={limit}
       count={equipments.total}
       onChangePage={handlePageChange}
       onChangeRowsPerPage={handleLimitChange}
      />
     </Box>
    </Container>
   )}
   <DeleteConfirmationDialog
    onClose={isDelete => {
     if (isDelete) {
      confirmRemove();
     } else {
      setOpenDeleteConfirm(false);
     }
    }}
    open={openDeleteConfirm}
    getMessage={getMessage}
   />
  </Page>
 );
}
export default EquipmentListView;

