import React, { memo } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import PropTypes from 'prop-types';
import { TRANSLATE_NS } from 'src/constants';
import { useTranslation } from 'react-i18next';
import { makeStyles, Button } from '@material-ui/core';
import clsx from 'clsx';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(1.2),
    textDecoration: 'underline'
  }
}));

function BackToMainPageButton({ className, ...rest }) {
  const classes = useStyles();
  const { t } = useTranslation([TRANSLATE_NS.TRANSLATIONS]);

  return (
    <Button
      component={RouterLink}
      to="/"
      className={clsx(classes.root, className)}
      {...rest}
    >
      {t('back to main page')}
    </Button>
  );
}

BackToMainPageButton.prototype = {
  className: PropTypes.string
};

export default memo(BackToMainPageButton);
