import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { TRANSLATE_NS } from 'src/constants';
import {
  Box,
  Button,
  Card,
  Container,
  Divider,
  TextField,
  makeStyles,
  Hidden,
  IconButton
} from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';

const useStyles = makeStyles(theme => ({
  root: {},
  queryButton: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(3),
    padding: theme.spacing(1.5, 1.5, 1.5, 1.5)
  },
  queryDivider: {
    margin: theme.spacing(0, 3)
  },
  queryCard: {
    minHeight: 60,
    borderRadius: theme.spacing(1),
    padding: theme.spacing(2)
  },
  queryField: {
    width: '95%',
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(1)
  }
}));

function Filter({ onSearch, className, ...rest }) {
  const classes = useStyles();
  const { t } = useTranslation([
    TRANSLATE_NS.EQUIPMENTS,
    TRANSLATE_NS.TRANSLATIONS
  ]);
  const [inputValue, setInputValue] = useState('');

  const handleQueryChange = event => {
    event.persist();
    setInputValue(event.target.value);
  };
  const handleSearch = event => {
    event.persist();
    onSearch(inputValue);
  };

  return (
    <Container
      maxWidth="md"
      className={clsx(className, classes.root)}
      {...rest}
    >
      <Card className={classes.queryCard}>
        <Box display="flex" alignItems="center">
          <TextField
            fullWidth={true}
            className={classes.queryField}
            onChange={handleQueryChange}
            value={inputValue}
            size="small"
            label={t('equipments:register medical tools list search')}
          />
          <Divider
            orientation="vertical"
            flexItem
            className={classes.queryDivider}
          />
          <Hidden smDown>
            <Button
              variant="contained"
              color="primary"
              className={classes.queryButton}
              //size="small"
              onClick={handleSearch}
            >
              <SearchIcon />
              {t('translations:search')}
            </Button>
          </Hidden>
          <Hidden only={['md', 'lg', 'xl']}>
            <IconButton color="primary" onClick={handleSearch}>
              <SearchIcon />
            </IconButton>
          </Hidden>
        </Box>
      </Card>
    </Container>
  );
}

Filter.propTypes = {
  onSearch: PropTypes.func.isRequired,
  className: PropTypes.string
};

export default Filter;
