import React, { Fragment } from 'react';
import { useHistory } from 'react-router-dom';
import clsx from 'clsx';
import {
 Card,
 Typography,
 makeStyles,
 CardHeader,
 Avatar
} from '@material-ui/core';
import ImageIcon from '@material-ui/icons/Image';
import { useTranslation } from 'react-i18next';
import { TRANSLATE_NS } from 'src/constants';
import { Skeleton } from '@material-ui/lab';
import { Edit2 as EditIcon, Trash2 as DeleteIcon } from 'react-feather';
import MoreButton from 'src/components/MoreButton';

const useStyles = makeStyles(theme => ({
 root: {
  minWidth: 178
 },
 cardHeaderRoot: {
  alignItems: 'unset'
 },
 cardHeaderWidthForGird: {
  [theme.breakpoints.up('md')]: {
   maxWidth: '55%'
  },
  [theme.breakpoints.down('sm')]: {
   maxWidth: '72%'
  }
 },
 cardHeaderWidthForList: {
  maxWidth: '90%'
 },
 imageLarge: {
  width: theme.spacing(8),
  height: theme.spacing(8)
 }
}));

function SetCard({
 loading,
 equipments: dataItem,
 onRemove,
 className,
 mode,
 ...rest
}) {
 const classes = useStyles();
 const { t } = useTranslation([TRANSLATE_NS.SETS, TRANSLATE_NS.TRANSLATIONS]);
 const history = useHistory();

 const settingButtons = [
  {
   translateKey: 'edit',
   click: () => {
    const url = `/equipments/${dataItem.id}`;
    history.push(url);
   },
   icon: <EditIcon />
  },
  {
   translateKey: 'delete',
   click: () => {
    if (onRemove) {
     onRemove(dataItem);
    }
   },
   icon: <DeleteIcon />
  }
 ];
 return (
  <Card className={clsx(classes.root, className)} {...rest}>
   <CardHeader
    classes={{
     root: classes.cardHeaderRoot,
     content: mode === 'grid' ? classes.cardHeaderWidthForGird : ''
    }}
    avatar={
     dataItem.image === '' ||
     dataItem.image === undefined ||
     dataItem.image === null ? (
      <Avatar variant="rounded" className={classes.imageLarge}>
       <ImageIcon />
      </Avatar>
     ) : (
      <Avatar
       alt="Remy Sharp"
       variant="rounded"
       src={dataItem.image}
       className={classes.imageLarge}
      />
     )
    }
    title={
     loading ? (
      <Fragment>
       <Skeleton
        animation="wave"
        variant="text"
        data-testid="skeleton-title"
       ></Skeleton>
       <Skeleton
        animation="wave"
        height={10}
        width="80%"
        style={{ marginBottom: 6 }}
       />
      </Fragment>
     ) : mode === 'grid' ? (
      <Typography color="textPrimary" variant="h5" noWrap>
       {dataItem.name}
      </Typography>
     ) : (
      <Typography color="textPrimary" variant="h5">
       {dataItem.name}
      </Typography>
     )
    }
    action={loading ? null : <MoreButton buttonList={settingButtons} />}
    subheader={dataItem.description}
   />
  </Card>
 );
}
// SetCard.propTypes = {
//  className: PropTypes.string,
//  set: PropTypes.shape({
//   name: PropTypes.string,
//   description: PropTypes.string,
//   active: PropTypes.bool
//  }),
//  loading: PropTypes.bool,
//  onRemove: PropTypes.func
// };
export default SetCard;
