import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import {
  Box,
  makeStyles,
  TextField,
  Typography,
  withWidth,
  Hidden
} from '@material-ui/core';
import pagingService from 'src/services/pagingService';
import ViewModuleIcon from '@material-ui/icons/ViewModule';
import { ToggleButtonGroup, ToggleButton } from '@material-ui/lab';
import { TRANSLATE_NS } from 'src/constants';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    alignItems: 'center',
    flexWrap: 'wrap',
    justifyContent: 'center',
    [theme.breakpoints.up('md')]: {
      justifyContent: 'space-between'
    }
  },
  sortByField: {
    marginRight: theme.spacing(1)
  },
  sortButton: {
    textTransform: 'none',
    letterSpacing: 0,
    marginRight: theme.spacing(2)
  },
  displayItemCounts: {}
}));

function Sorting({
  itemCounts,
  onSortChange,
  onModeChange,
  className,
  ...rest
}) {
  const classes = useStyles();
  const { t } = useTranslation([
    TRANSLATE_NS.TRANSLATIONS,
    TRANSLATE_NS.EQUIPMENTS
  ]);
  const sortOptions = pagingService.sortOptions;
  const [sort, setSort] = useState(sortOptions[0].value);
  const [mode, setMode] = useState('grid');

  const handleSortChange = event => {
    event.persist();
    setSort(event.target.value);
    onSortChange(event.target.value);
  };
  const handleModeChange = (event, value) => {
    event.persist();
    setMode(value);
    onModeChange(value);
  };

  return (
    <Box className={clsx(className, classes.root)} {...rest}>
      <Hidden smDown>
        <Box display="flex" alignItems="center">
          <Typography
            className={classes.displayItemCounts}
            variant="h5"
            color="textPrimary"
          >
            {`${t('translations:showing')} ${itemCounts} ${t(
              'equipments:register medical tools'
            )}`}
          </Typography>
        </Box>
      </Hidden>
      <Box display="flex" alignItems="center">
        <TextField
          label={t('translations:sort by')}
          name="sort"
          onChange={handleSortChange}
          select
          SelectProps={{ native: true }}
          value={sort}
          variant="outlined"
          size="small"
          className={classes.sortByField}
        >
          {sortOptions.map(option => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <Hidden mdDown>
          <Box flexGrow={1} />
          <ToggleButtonGroup
            exclusive
            onChange={handleModeChange}
            size="small"
            value={mode}
          >
            <ToggleButton value="grid">
              <ViewModuleIcon color="primary" />
            </ToggleButton>
          </ToggleButtonGroup>
        </Hidden>
      </Box>
    </Box>
  );
}

Sorting.propTypes = {
  itemCounts: PropTypes.number,
  onSortChange: PropTypes.func,
  onModeChange: PropTypes.func,
  className: PropTypes.string,
  width: PropTypes.oneOf(['lg', 'md', 'sm', 'xl', 'xs']).isRequired
};

Sorting.defaultProps = {
  itemCounts: 0,
  onSortChange: () => {},
  onModeChange: () => {},
  className: ''
};

export default withWidth()(Sorting);
