import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { Box, makeStyles, TablePagination } from '@material-ui/core';
import { PAGING } from 'src/constants';
import { TRANSLATE_NS } from 'src/constants';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles(theme => ({
 root: {}
}));

function Paging({
 page,
 limit,
 count,
 onChangePage,
 onChangeRowsPerPage,
 className,
 ...rest
}) {
 const classes = useStyles();
 const { t } = useTranslation([TRANSLATE_NS.TRANSLATIONS, TRANSLATE_NS.SETS]);
 const pageSizes = PAGING.PAGE_SIZES;

 const handleLimitChange = event => {
  event.persist();
  onChangeRowsPerPage(event.target.value);
 };
 const handlePageChange = (event, newPage) => {
  event.persist();
  onChangePage(newPage + 1);
 };

 const displayPage = page - 1; // default the start page is 0

 return (
  <Box
   className={clsx(className, classes.root)}
   {...rest}
   display="flex"
   justifyContent="center"
  >
   <TablePagination
    component="div"
    count={count}
    onChangePage={handlePageChange}
    onChangeRowsPerPage={handleLimitChange}
    page={displayPage}
    rowsPerPage={limit}
    rowsPerPageOptions={pageSizes}
    labelRowsPerPage={t('translations:rows per page')}
    nextIconButtonText={t('translations:next page')}
    backIconButtonText={t('translations:previous page')}
   />
  </Box>
 );
}

Paging.propTypes = {
 page: PropTypes.number.isRequired,
 limit: PropTypes.number.isRequired,
 count: PropTypes.number.isRequired,
 onChangePage: PropTypes.func.isRequired,
 onChangeRowsPerPage: PropTypes.func.isRequired,
 className: PropTypes.string
};

Paging.defaultProps = {
 page: PAGING.PAGE,
 limit: PAGING.PAGE_SIZE,
 count: 0,
 onChangePage: () => {},
 onChangeRowsPerPage: () => {},
 className: ''
};

export default Paging;
