import React from 'react';
import PropTypes from 'prop-types';
import { Link as RouterLink } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { TRANSLATE_NS } from 'src/constants';
import clsx from 'clsx';
import AddCircleOutlineOutlinedIcon from '@material-ui/icons/AddCircleOutlineOutlined';
import { Box, Button, Grid, makeStyles, Container } from '@material-ui/core';
import BackToMainPageButton from 'src/components/BackToMainPageButton';
import PageName from 'src/components/PageName';
import Filter from './Filter';

const useStyles = makeStyles(theme => ({
  root: {
    minWidth: '100%',
    height: 170,
    background: theme.palette.background.header
  },
  actionIcon: {
    marginRight: theme.spacing(1)
  },
  previousButton: {
    '&:hover': {
      textDecoration: 'underline'
    }
  },
  titleRow: {
    paddingTop: theme.spacing(1),
    color: theme.palette.text.primary
  },
  addButton: {
    padding: theme.spacing(1.75, 1.5, 1.75, 1.5)
  },
  addIconButton: {
    marginRight: theme.spacing(1)
  },
  searchWrapper: {
    zIndex: theme.zIndex.drawer + 100,
    marginTop: theme.spacing(1)
  }
}));

function Header({ onSearch, className, ...rest }) {
  const classes = useStyles();
  const { t } = useTranslation([
    TRANSLATE_NS.EQUIPMENTS,
    TRANSLATE_NS.TRANSLATIONS
  ]);

  return (
    <div className={clsx(classes.root, className)} {...rest}>
      <Container maxWidth="md">
        <Grid
          alignItems="center"
          container
          justify="space-between"
          className={classes.titleRow}
        >
          <Grid item>
            <PageName>{t('equipments:register medical tools')}</PageName>
          </Grid>
          <Grid item>
            <BackToMainPageButton className={classes.previousButton} />
          </Grid>
        </Grid>
        <Box pt={1}>
          <Button
            variant="contained"
            color="secondary"
            className={classes.addButton}
            component={RouterLink}
            to="/equipments/create"
          >
            <AddCircleOutlineOutlinedIcon className={classes.addIconButton} />
            {t('equipments:add register medical tools')}
          </Button>
        </Box>
      </Container>
      <Filter onSearch={onSearch} className={classes.searchWrapper} />
    </div>
  );
}

Header.propTypes = {
  onSearch: PropTypes.func,
  className: PropTypes.string
};

export default Header;
