export const operators = {
  equals: "eq",
  notEquals: "neq",
  greaterThan: "gt",
  greaterThanOrEquals: "gte",
  lessThan: "lt",
  lessThanOrEquals: "lte",
  contains: "ct",
  startWith: "st"
}

export const logic = {
  and: "and",
  or: "or"
}

export const initFilter = ({field, operator = operators.contains, value = ""}) => {
  if(!field) return null  
  else  return { field, operator, value }
}

export const initFilters = ({logic = "and", filters = []}) => {
  return { logic, filters }
}