import React from 'react';
import PropTypes from 'prop-types';
import { v4 as uuidv4 } from 'uuid';
import { useTranslation, Trans } from 'react-i18next';
import { TRANSLATE_NS } from 'src/constants';
import Button from '@material-ui/core/Button';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import Dialog from '@material-ui/core/Dialog';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { DialogContentText, SvgIcon, useMediaQuery, Box } from '@material-ui/core';
import { Trash2 as DeleteIcon } from 'react-feather';

const useStyles = makeStyles((theme) => ({
  root: {

  },
  dialogTitle: {
    "& h2": {
      display: 'flex'
    }
  }
}));

function DeleteConfirmationDialog(props) {
  const { onClose, open, getMessage, ...other } = props;
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS]);
  const classes = useStyles();

  React.useEffect(() => {
    if (!open) {
    }
  }, [open]);

  const handleCancel = () => {
    onClose(false);
  };

  const handleOk = () => {
    onClose(true);
  };
  
  let message = "";
  if(getMessage){
    const result = getMessage();
    if(result) message = result;
  }

  const dialogId = uuidv4();

  return (
    <Dialog
      fullScreen={fullScreen}
      disableBackdropClick
      disableEscapeKeyDown
      aria-labelledby={dialogId}
      open={open}
      {...other}
    >
      <DialogTitle id={dialogId} className={classes.dialogTitle}>
        <SvgIcon>
          <DeleteIcon fontSize="small" />
        </SvgIcon>
        <Box 
          alignSelf="center"
          component="span"
          ml={1}
        >
          {t("translations:delete title dialog")}
        </Box>
      </DialogTitle>
      <DialogContent dividers>
        <DialogContentText>
          <Trans i18nKey="are you sure you want to remove"values={{itemName: message}}></Trans>
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button autoFocus onClick={handleCancel} color="primary">
          {t("translations:cancel")}
        </Button>
        <Button autoFocus onClick={handleOk} color="primary">
          {t("translations:yes")}
        </Button>
      </DialogActions>
    </Dialog>
  );
}

DeleteConfirmationDialog.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  getMessage: PropTypes.func
};

export default DeleteConfirmationDialog;