import axios from 'src/utils/axios';
import { initFilter } from 'src/utils/filters';
import * as handleResponse from './response';

class EquipmentService {
 getAll = () =>
  new Promise((resolve, reject) => {
   axios
    .get('/api/inventories/medicalDevices')
    .then(response => {
     const result = handleResponse.accessResponse(response);
     resolve(result);
    })
    .catch(error => {
     const result = handleResponse.accessError(error);
     reject(result);
    });
  });

 getById = equipmentId =>
  new Promise((resolve, reject) => {
   axios
    .get('/api/inventories/medicalDevices/' + equipmentId, {
     params: { equipmentId }
    })
    .then(response => {
     const result = handleResponse.accessResponse(response);
     resolve(result);
    })
    .catch(error => {
     const result = handleResponse.accessError(error);
     reject(result);
    });
  });

 getList = (filter, sort, page, pageSize) =>
  new Promise((resolve, reject) => {
   const customFilter =
    filter && filter !== '' ? initFilter({ field: 'name', value: filter }) : '';

   axios
    .get('/api/inventories/medicalDevices', {
     params: { filter: customFilter, sort, page, pageSize }
    })
    .then(response => {
     const result = handleResponse.accessResponse(response);
     resolve(result);
    })
    .catch(error => {
     const result = handleResponse.accessError(error);
     reject(result);
    });
  });

 insert = dataItem =>
  new Promise((resolve, reject) => {
   axios
    .post('/api/inventories/medicalDevices', dataItem)
    .then(response => {
     resolve(response.data);
    })
    .catch(error => {
     reject({
      resultFlag: false,
      value: null,
      errorCode: 'Something is wrong.',
      errorMessage: error.message,
      activityId: null
     });
    });
  });

 update = dataItem =>
  new Promise((resolve, reject) => {
   axios
    .put('/api/inventories/medicalDevices', dataItem)
    .then(response => {
     resolve(response.data);
    })
    .catch(error => {
     reject({
      resultFlag: false,
      value: null,
      errorCode: 'Something is wrong.',
      errorMessage: error.message,
      activityId: null
     });
    });
  });

 remove = dataItem =>
  new Promise((resolve, reject) => {
   console.log('remove: ', dataItem);

   axios
    .delete('/api/inventories/medicalDevices/' + dataItem.id, {
     ...dataItem
    })
    .then(response => {
     console.log(response);
     const result = handleResponse.accessResponse(response);
     resolve(result);
    })
    .catch(error => {
     const result = handleResponse.accessError(error);
     reject(result);
    });
  });
}

const equipmentService = new EquipmentService();

export default equipmentService;
