import React, {
    useRef,
    useState,
    memo
  } from 'react';
  import PropTypes from 'prop-types';
  import {
    ListItemIcon,
    ListItemText,
    Tooltip,
    IconButton,
    Menu,
    MenuItem,
    makeStyles
  } from '@material-ui/core';
import MoreIcon from '@material-ui/icons/MoreVert';
import { TRANSLATE_NS } from 'src/constants';
import {useTranslation} from "react-i18next";
import {uid} from "react-uid";

const useStyles = makeStyles(() => ({
  menu: {
    //width: 160,
    maxWidth: '100%'
  }
}));

function MoreButton({ buttonList, ...rest }) {
  const classes = useStyles();
  const moreRef = useRef(null);
  const [openMenu, setOpenMenu] = useState(false);
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS])

  const handleMenuOpen = () => {
    setOpenMenu(true);
  };

  const handleMenuClose = () => {
    setOpenMenu(false);
  };

  const handleItemClick = (buttonItem) => {
    if(typeof buttonItem.click === "function") {
      buttonItem.click();
      setOpenMenu(false);
    }
  };

  return (
    <>
      <Tooltip title={t('translations:more options')}>
        <IconButton
          {...rest}
          onClick={handleMenuOpen}
          ref={moreRef}
        >
          <MoreIcon fontSize="small" />
        </IconButton>
      </Tooltip>
      <Menu
        anchorEl={moreRef.current}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'left'
        }}
        onClose={handleMenuClose}
        open={openMenu}
        PaperProps={{ className: classes.menu }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'left'
        }}
      >
        {
          buttonList && buttonList.map((btn)=> {
            return (
              <MenuItem 
                onClick={()=> handleItemClick(btn)} 
                key={uid(btn)} 
                value={btn}
              >
                <ListItemIcon>
                  {btn.icon}
                </ListItemIcon>
                <ListItemText primary={t(btn.translateKey)} />
              </MenuItem>
            );
          })
        }
      </Menu>
    </>
  );
}

MoreButton.propTypes = {
  className: PropTypes.string,
  buttonList: PropTypes.array.isRequired
};

MoreButton.defaultProps = {
  className: '',
  buttonList: []
};

export default memo(MoreButton);
